"use client";

import { useEffect, useState } from "react";
import { useRouter } from "next/navigation";
import { Card, CardContent } from "@/components/ui/card";
import { Button } from "@/components/ui/button";
import { Tabs, TabsContent, TabsList, TabsTrigger } from "@/components/ui/tabs";
import { Input } from "@/components/ui/input";
import { Label } from "@/components/ui/label";
import { Checkbox } from "@/components/ui/checkbox";
import AdminSidebar from '@/app/admin/components/AdminSidebar';
import { supabase } from "@/lib/supabase";

const supabase = createClient(
  process.env.NEXT_PUBLIC_SUPABASE_URL!,
  process.env.NEXT_PUBLIC_SUPABASE_ANON_KEY!
);

export default function CreateStorePage() {
  const router = useRouter();
  const [storeType, setStoreType] = useState("hub");
  const [taskTypes, setTaskTypes] = useState([]);
  const [storeDetails, setStoreDetails] = useState({
    name: "",
    city: "",
    address: "",
    latitude: "",
    longitude: "",
    manager_name: "",
    manager_number: "",
    alias: "",
    password: "",
  });
  const [taskCapacities, setTaskCapacities] = useState({});
  const [hubOptions, setHubOptions] = useState([]);
  const [selectedHubs, setSelectedHubs] = useState([]);

  useEffect(() => {
    const fetchData = async () => {
      const { data: tasks } = await supabase
        .from("task_types")
        .select("*")
        .eq(storeType === "hub" ? "allowed_in_hub" : "allowed_in_garage", true);
      setTaskTypes(tasks || []);
      if (storeType === "garage") {
        const { data: hubs } = await supabase
          .from("store_admin")
          .select("id, name")
          .eq("type", "hub");
        setHubOptions(hubs || []);
      }
    };
    fetchData();
  }, [storeType]);

  const handleInputChange = (e) => {
    const { name, value } = e.target;
    setStoreDetails((prev) => ({ ...prev, [name]: value }));
  };

  const handleCapacityChange = (taskId, value) => {
    setTaskCapacities((prev) => ({ ...prev, [taskId]: parseInt(value || "0", 10) }));
  };

  const handleHubToggle = (hubId) => {
    setSelectedHubs((prev) =>
      prev.includes(hubId) ? prev.filter((id) => id !== hubId) : [...prev, hubId]
    );
  };

  const handleSubmit = async () => {
    const { data: insertedStore, error: storeError } = await supabase
      .from("store_admin")
      .insert([{ ...storeDetails, type: storeType }])
      .select()
      .single();

    if (storeError) {
      console.error("Insert error:", storeError.message);
      return alert("Store creation failed: " + storeError.message);
    }

    const taskPayload = taskTypes.map((t) => ({
      store_id: insertedStore.id,
      task_type_id: t.id,
      capacity: taskCapacities[t.id] || 0,
    }));

    await supabase.from("store_task_capacities").insert(taskPayload);

    if (storeType === "garage" && selectedHubs.length > 0) {
      const tags = selectedHubs.map((hubId) => ({
        garage_id: insertedStore.id,
        hub_id: hubId,
      }));
      await supabase.from("garage_hub_tags").insert(tags);
    }

    router.push("/admin/stores");
  };

  return (
    <div className="flex min-h-screen">
      <AdminSidebar />
      <div className="flex-1 p-4 md:p-8">
        <Button variant="outline" onClick={() => router.back()} className="mb-4">
          ← Back
        </Button>
        <Card>
          <CardContent className="p-6">
            <Tabs defaultValue="details">
              <TabsList>
                <TabsTrigger value="details">Store Details</TabsTrigger>
                <TabsTrigger value="tasks">Task Capacities</TabsTrigger>
                {storeType === "garage" && <TabsTrigger value="hubs">Tag Hubs</TabsTrigger>}
              </TabsList>
              <TabsContent value="details">
                <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                  <div>
                    <Label>Name</Label>
                    <Input name="name" onChange={handleInputChange} />
                  </div>
                  <div>
                    <Label>Alias</Label>
                    <Input name="alias" onChange={handleInputChange} />
                  </div>
                  <div>
                    <Label>City</Label>
                    <Input name="city" onChange={handleInputChange} />
                  </div>
                  <div>
                    <Label>Address</Label>
                    <Input name="address" onChange={handleInputChange} />
                  </div>
                  <div>
                    <Label>Latitude</Label>
                    <Input name="latitude" onChange={handleInputChange} />
                  </div>
                  <div>
                    <Label>Longitude</Label>
                    <Input name="longitude" onChange={handleInputChange} />
                  </div>
                  <div>
                    <Label>Manager Name</Label>
                    <Input name="manager_name" onChange={handleInputChange} />
                  </div>
                  <div>
                    <Label>Manager Number</Label>
                    <Input name="manager_number" onChange={handleInputChange} />
                  </div>
                  <div>
                    <Label>Password</Label>
                    <Input name="password" type="password" onChange={handleInputChange} />
                  </div>
                  <div>
                    <Label>Type</Label>
                    <select
                      className="w-full border rounded p-2"
                      onChange={(e) => setStoreType(e.target.value)}
                      value={storeType}
                    >
                      <option value="hub">Hub</option>
                      <option value="garage">Garage</option>
                    </select>
                  </div>
                </div>
              </TabsContent>
              <TabsContent value="tasks">
                <div className="space-y-4">
                  {taskTypes.map((task) => (
                    <div key={task.id} className="grid grid-cols-1 md:grid-cols-3 items-center gap-4">
                      <Label>{task.name}</Label>
                      <Input
                        type="number"
                        value={taskCapacities[task.id] || 0}
                        onChange={(e) => handleCapacityChange(task.id, e.target.value)}
                      />
                      <span>{task.slot_type}</span>
                    </div>
                  ))}
                </div>
              </TabsContent>
              <TabsContent value="hubs">
                <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                  {hubOptions.map((hub) => (
                    <div key={hub.id} className="flex items-center space-x-2">
                      <Checkbox
                        checked={selectedHubs.includes(hub.id)}
                        onCheckedChange={() => handleHubToggle(hub.id)}
                      />
                      <Label>{hub.name}</Label>
                    </div>
                  ))}
                </div>
              </TabsContent>
            </Tabs>
            <Button className="mt-6 w-full md:w-auto" onClick={handleSubmit}>
              Create Store
            </Button>
          </CardContent>
        </Card>
      </div>
    </div>
  );
}
